<template>
    <div class="card">
        <div class="header">
            <h2 class="top_info_heading">Utility Chain Txs</h2>
        </div>
        <table>
            <thead>
                <tr>
                    <th>Hash</th>
                    <th>Amount in DJT</th>
                    <th>Timestamp</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="transaction in transactions" :key="transaction.hash">
                    <td>{{ transaction.hash }}</td>
                    <td>
                        {{ formatValue(transaction.value) }} DJT
                        <svg
                            width="18"
                            height="18"
                            viewBox="0 0 24 24"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <path
                                fill-rule="evenodd"
                                clip-rule="evenodd"
                                d="M7.53734 4.21455C7.83307 3.92451 8.30792 3.92912 8.59795 4.22484L11.7855 7.47484C12.0715 7.76651 12.0715 8.23349 11.7855 8.52516L8.59795 11.7752C8.30792 12.0709 7.83307 12.0755 7.53734 11.7855C7.24162 11.4954 7.23701 11.0206 7.52705 10.7248L9.46391 8.75H2.75C2.33579 8.75 2 8.41421 2 8C2 7.58579 2.33579 7.25 2.75 7.25H9.46391L7.52705 5.27516C7.23701 4.97943 7.24162 4.50458 7.53734 4.21455ZM16.4627 12.2145C16.7584 12.5046 16.763 12.9794 16.473 13.2752L14.5361 15.25H21.25C21.6642 15.25 22 15.5858 22 16C22 16.4142 21.6642 16.75 21.25 16.75H14.5361L16.473 18.7248C16.763 19.0206 16.7584 19.4954 16.4627 19.7855C16.1669 20.0755 15.6921 20.0709 15.402 19.7752L12.2145 16.5252C11.9285 16.2335 11.9285 15.7665 12.2145 15.4748L15.402 12.2248C15.6921 11.9291 16.1669 11.9245 16.4627 12.2145Z"
                                fill="#000"
                            ></path>
                        </svg>
                    </td>
                    <td>{{ formatDate(transaction.createdAt) }}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
export default {
    data() {
        return {
            transactions: [],
        }
    },
    created() {
        this.fetchData()
        this.refreshInterval = setInterval(this.refreshData, 5000) // Refresh every 5 seconds
    },
    beforeDestroy() {
        clearInterval(this.refreshInterval)
    },
    methods: {
        async fetchData() {
            try {
                const response = await fetch(
                    'https://explorer.uksouth.cloudapp.azure.com/v2/ctransactions?limit=10&sort=timestamp-desc'
                )
                if (response.ok) {
                    const data = await response.json()
                    this.transactions = data.Transactions
                } else {
                    console.error('API request failed:', response.statusText)
                }
            } catch (error) {
                console.error('An error occurred:', error)
            }
        },
        formatDate(date) {
            return new Date(date).toLocaleString()
        },
        formatValue(value) {
            const etherValue = value / Math.pow(10, 18)
            return etherValue.toLocaleString(undefined, {
                minimumFractionDigits: 2,
                maximumFractionDigits: 2,
            })
        },
        refreshData() {
            this.fetchData()
        },
    },
}
</script>

